import React, { useEffect, useState } from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import ShoppingCartPage from "./ShoppingCartPage";
import { Link } from "react-router-dom";
import { getAllCategories, getAllProducts, scrollToTop } from "./Globals/GlobalFunctions";
import { useDispatch, useSelector } from "react-redux";

function Home() {
  const dispatch = useDispatch();

  const [availableCategories, setAvailableCategories] = useState("");
  const productListArr = useSelector((state) => state.productListArr);
  const shoppingCartArr = useSelector((state) => state.shoppingCartArr);

  const options = {
    dots: true,
    arrows: false,
    autoplay: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
  };

  useEffect(() => {
    fetchProductList();
    fetchCategoryList();
  }, [])

  async function fetchProductList() {
    const data = await getAllProducts();
    if (data) {
      dispatch({
        type: "SET_PRODUCTS",
        productListArr: data,
      })
    }
  }

  async function fetchCategoryList() {
    const data = await getAllCategories();
    if (data) {
      setAvailableCategories(data);
    }
  }

  const handleShoppingCart = (item) => {
    const existingItem = shoppingCartArr.find((ele) => ele.id === item.id);

    if (!existingItem) {
      dispatch({
        type: "SET_CART",
        shoppingCartArr: [...shoppingCartArr, item],
      })
    } else {
      const updatedCart = shoppingCartArr.filter((ele) => ele.id !== item.id);
      dispatch({
        type: "SET_CART",
        shoppingCartArr: updatedCart,
      })
    }

  }

  useEffect(() => {
    console.log(shoppingCartArr)
  }, [shoppingCartArr])

  return (
    <>
      <section className="homeBanner">
        <div className="container">
          <div className="row">
            <div className="col-12 px-0">
              <Slider {...options}>
                <div className="imgWrapper">
                  <img src="https://www.karagiri.com/cdn/shop/collections/website_desktop_banners.webp?v=1704467286" />
                </div>
                <div className="imgWrapper">
                  <img src="https://i.imgur.com/gnqsQKq.png" />
                </div>
                <div className="imgWrapper">
                  <img src="https://i.imgur.com/gnqsQKq.png" />
                </div>
              </Slider>
            </div>
          </div>
        </div>
      </section>
      <section className="featuredProducts">
        <div className="container">
          <div className="row gy-3 gx-2 gx-xl-3">
            <div className="heading">
              <h2>Featured Products</h2>
            </div>
            {productListArr.map((item, index) => {
              return (
                <div key={index} className="col-xl-3 col-md-4 col-6">
                  <div className="productItem">
                    <div className="imgWrapper">
                      <img src={item.image} />
                    </div>
                    <div className="content">
                      <p className="mb-0">
                        {
                          Array.from({ length: Math.round(item.rating.rate) }, (_, i) => (
                            <i key={i} className="fa-solid fa-star"></i>
                          ))
                        }
                        <span className="originalPrice ms-2">({item.rating.count})</span>
                      </p>
                      <h5><Link to={`/product-detail/${item.id}`} onClick={scrollToTop}>{item.title}</Link></h5>
                      <p className="offerPrice">
                        ${item.price} <del className="originalPrice">{(item.price / (1 - 40 / 100)).toFixed(2)}</del>
                      </p>
                      <span className="discount">40% Off</span>
                      <div>
                        <button onClick={() => handleShoppingCart(item)} className={shoppingCartArr.find((ele) => ele.id === item.id) ? "btn1 selected w-100" : "btn1 w-100"}>{shoppingCartArr.find((ele) => ele.id === item.id) ? <i class="fa-regular fa-check text-white me-1"></i> : ""} {shoppingCartArr.find((ele) => ele.id === item.id) ? "Added" : "Add To Cart"}</button>
                      </div>
                    </div>
                  </div>
                </div>
              )
            })}
          </div>
        </div>
      </section>

      <section className="smallCTA">
        <div className="container">
          <div className="row">
            <div className="col-xl-12">
              <div className="imgWrapper">
                <img src={require("../Assets/images/ctabg.webp")} />
              </div>
            </div>
          </div>
        </div>
      </section>

      <section className="featuredProducts2">
        <div className="container">
          <div className="row gy-3">
            <div className="heading">
              <h2>Deal of the Day</h2>
            </div>
            {productListArr.filter((item) => item.category === "women's clothing").slice(-4)
              .map((item, index) => {
                return (
                  <div key={index} className="col-xl-3 col-md-4 col-6">
                    <div className="productItem">
                      <Link to={`/product-detail/${item.id}`} onClick={scrollToTop}>
                        <div className="imgWrapper">
                          <img src={item.image} />
                        </div>
                        <div className="content">
                          <h3 className="position-relative">Under ${(item.price + 50).toFixed(2)}</h3>
                        </div>
                      </Link>
                    </div>
                  </div>
                )
              })}
          </div>
        </div>
      </section>

      <section className="weddingSection">
        <div className="container">
          <div className="row">
            <div className="heading">
              <h2>Wedding Vibes</h2>
            </div>
            {productListArr.filter((item) => item.category === "women's clothing").slice(0, 4)
              .map((item, index) => {
                return (
                  <div key={index} className="col-xl-3 col-md-4 col-6">
                    <Link to={`/product-detail/${item.id}`} onClick={scrollToTop}>
                      <div className="productItem">
                        <div className="imgWrapper">
                          <img
                            src={require("../Assets/images/goldenFrame.png")}
                            className="frame"
                          />
                          <img src={item.image} />
                        </div>
                      </div>
                      <div className="content">
                        <h5 className="position-relative text-dark">{item.title}</h5>
                      </div>
                    </Link>
                  </div>
                )
              })}
          </div>
        </div>
      </section >

      <section className="featuredProducts">
        <div className="container">
          <div className="row gy-3 gx-2 gx-xl-3">
            <div className="heading">
              <h2>Best Sellers</h2>
            </div>
            {productListArr.filter(item => item.rating.rate > 4.5).slice(0, 4)
              .map((item, index) => {
                return (
                  <div key={index} className="col-xl-3 col-md-4 col-6">
                    <div className="productItem">
                      <div className="imgWrapper">
                        <img src={item.image} />
                      </div>
                      <div className="content">
                        <p className="mb-0">
                          {Array.from({ length: item.rating.rate }, (_, i) => (
                            <i key={i} className="fa-solid fa-star"></i>
                          ))}
                        </p>
                        <h5><Link to={`/product-detail/${item.id}`} onClick={scrollToTop}>{item.title}</Link></h5>
                        <p className="offerPrice">
                          ${item.price} <del className="originalPrice">{(item.price / (1 - 40 / 100)).toFixed(2)}</del>
                        </p>
                        <span className="discount">40% Off</span>
                        <div>
                          <button onClick={() => handleShoppingCart(item)} className={shoppingCartArr.find((ele) => ele.id === item.id) ? "btn1 selected w-100" : "btn1 w-100"}>{shoppingCartArr.find((ele) => ele.id === item.id) ? <i class="fa-regular fa-check text-white me-1"></i> : ""} {shoppingCartArr.find((ele) => ele.id === item.id) ? "Added" : "Add To Cart"}</button>
                        </div>
                      </div>
                    </div>
                  </div>
                )
              })}
          </div>
        </div>
      </section>

      <section className="weddingSection">
        <div className="container">
          <div className="row">
            <div className="heading">
              <h2>Explore More</h2>
            </div>
            {productListArr.filter(item => item.category === "jewelery").slice(0, 4)
              .map((item, index) => {
                return (
                  <div key={index} className="col-xl-3 col-md-4 col-6">
                    <Link to={`/product-detail/${item.id}`} onClick={scrollToTop}>
                      <div className="productItem">
                        <div className="imgWrapper">
                          <img
                            src={require("../Assets/images/squareFrame.png")}
                            className="frame"
                          />
                          <img src={item.image} />
                        </div>
                      </div>
                      <div className="content">
                        <h5 className="position-relative text-dark">{item.title}</h5>
                      </div>
                    </Link>
                  </div>
                )
              })}
          </div>
        </div>
      </section>
      {shoppingCartArr.length > 0 ? <ShoppingCartPage /> : ""}
    </>
  );
}

export default Home;
