import React, { useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import { Link } from 'react-router-dom'

function ShoppingCartPage() {
    const shoppingCartArr = useSelector((state) => state.shoppingCartArr);
    const [subTotal, setSubTotal] = useState(0)

    useEffect(() => {
        handleCartSubTotal()
    }, [shoppingCartArr])

    const handleCartSubTotal = () => {
        const subTotal = shoppingCartArr.reduce((accumulator, currentItem) => parseFloat(accumulator) + parseFloat(currentItem.price), 0);
        setSubTotal(subTotal);
    }

    return (
        <div className='cartCanvas'>
            <div className='container'>
                <div className='row'>
                    {/* <div className='col-xl-12'>
                        <div className='heading'>
                            <h3>Your Cart</h3>
                            <p>Enim irure et sint adipisicing nulla minim velit sint cillum consequat commodo duis in labore.</p>
                        </div>
                    </div> */}
                    <div className='col-xl-12'>
                        <div className='cartSubTotal'>
                            <div className='col-8 text-start'>
                                <p>Subtotal</p>
                                <h5>₹ {subTotal.toFixed(2)}</h5>
                            </div>
                            <div className='col-4 my-auto ms-auto'>
                                <Link><i class="fa-regular fa-arrow-right"></i></Link>
                            </div>
                        </div>
                        {shoppingCartArr?.map((item, index) => {
                            return (
                                <div key={index} className='cartListItem'>
                                    <div className='col-12'>
                                        <div className='imgWrapper'>
                                            <img src={item.image} />
                                        </div>
                                    </div>
                                    <div className='col-12'>
                                        <div className='content h-100'>
                                            <h5>$ {item.price}</h5>
                                            {/* <h5>ROSHNI BHATIA in Wine Purple Woven Kanjivaram Saree</h5> */}
                                            <div className='d-flex justify-content-center mt-auto'>
                                                <div className="qtySelect">
                                                    <div>
                                                        <i class="fa-solid fa-minus"></i>
                                                    </div>
                                                    <input type="text" value={1} disabled={true} />
                                                    <div>
                                                        <i class="fa-solid fa-plus"></i>
                                                    </div>
                                                </div>

                                            </div>
                                        </div>
                                    </div>
                                </div>
                            )
                        })}
                    </div>
                    {/* <div className='col-xl-3'>

                    </div> */}
                </div>
            </div>
        </div>
    )
}

export default ShoppingCartPage