import axios from "axios";

const baseName = "https://fakestoreapi.com/";

const axiosInstance = axios.create({
    baseURL: baseName,
    headers: { "Content-Type": "application/json" },
})

export async function login(userName, passWord) {
    const parsedData = { username: userName, password: passWord }
    return axiosInstance
        .post("auth/login", parsedData)
        .then((res) => {
            return res.data;
        })
        .catch((err) => {
            return null
        })
}

export async function getAllProducts() {
    return axiosInstance.get("products").then((res) => { return res.data });
}

export async function getAllCategories() {
    return axiosInstance.get(`products/categories`).then((res) => { return res });
}

export function scrollToTop() {
    window.scroll(0, 0);
}




