var productListArr = [];
var urlParam = "";
var shoppingCartArr = [];
var token = "";

const initialState = {
    productListArr,
    urlParam,
    shoppingCartArr,
    token,
}

const counterReducer = (state = initialState, action) => {
    switch (action.type) {
        case "SET_PRODUCTS":
            return { ...state, productListArr: action.productListArr };
        case "SET_URLPARAM":
            return { ...state, urlParam: action.urlParam };
        case "SET_CART":
            return { ...state, shoppingCartArr: action.shoppingCartArr };
        case "SET_TOKEN":
            return { ...state, token: action.token };
        default:
            return state;
    }
};

export default counterReducer;
